// Action Types
export const ADD_ITEM = "ADDITEM";
export const DELETE_ITEM = "DELITEM";

// Action Creator for Adding Item to Cart
export const addCart = (product) => {
  return {
    type: ADD_ITEM,
    payload: product,
  };
};

// Action Creator for Deleting Item from Cart
export const delCart = (productId) => {
  return {
    type: DELETE_ITEM,
    payload: productId, // Assuming productId is used for identification
  };
};
