// Retrieve initial state from localStorage if available
const getInitialCart = () => {
  const storedCart = localStorage.getItem("cart");
  return storedCart ? JSON.parse(storedCart) : [];
};

const handleCart = (state = getInitialCart(), action) => {
  const product = action.payload;
  let updatedCart;

  switch (action.type) {
    case "ADDITEM":
      // Check if product already in cart
      const existingProduct = state.find((item) => item.id === product.id);
      if (existingProduct) {
        // Increase the quantity if the product exists
        updatedCart = state.map((item) =>
          item.id === product.id ? { ...item, qty: item.qty + 1 } : item
        );
      } else {
        // Add new product with quantity 1
        updatedCart = [...state, { ...product, qty: 1 }];
      }
      // Update localStorage
      localStorage.setItem("cart", JSON.stringify(updatedCart));
      return updatedCart;

    case "DELITEM":
      const productInCart = state.find((item) => item.id === product.id);
      if (productInCart) {
        if (productInCart.qty === 1) {
          // Remove product if quantity is 1
          updatedCart = state.filter((item) => item.id !== productInCart.id);
        } else {
          // Decrease the quantity
          updatedCart = state.map((item) =>
            item.id === product.id ? { ...item, qty: item.qty - 1 } : item
          );
        }
        // Update localStorage
        localStorage.setItem("cart", JSON.stringify(updatedCart));
        return updatedCart;
      }
      return state; // Return unchanged state if product is not found

    default:
      return state;
  }
};

export default handleCart;
